import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import {  getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import LaporanHutangExel from "./exel/LaporanHutangExel";
import LaporanHutangPdf from "./pdf/LaporanHutangPdf";
import Loading from 'react-fullscreen-loading';
class HeadFormHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    LaporanHutangPdf(this.props.LaporanHutang);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Kode Sales : {row.kode_sales} <br />
              Nama Customer : {row.nama_customer} <br />
              No Hp : {row.no_hp} <br />
              Alamat Customer : {row.alamat_customer} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    let databarang = [];
    this.props.LaporanHutang.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        databarang.push(item)
        // item.detail_barang.forEach((detail_barang, index) => {
        //   total_ongkos = total_ongkos +  parseInt(detail_barang.ongkos) * parseInt(detail_barang.jumlah);
        //   total_jumlah = total_jumlah + detail_barang.jumlah;
        //   total_berat = total_berat + detail_barang.berat;
        // })
        // total_barang = total_barang + item.detail_barang.length;
      });
    });
    const columns = [
      {
        dataField: "no_faktur_hutang",
        text: "No Faktur Hutang",
        footerAttrs: {
          colSpan: "2",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Transaksi Hutang : {databarang.length} </div>;
        },
      },
      {
        dataField: "tgl_hutang",
        text: "Tanggal Hutang",
      },
   
      // {
      //   dataField: "kode_sales",
      //   text: "Kode Sales",
      // },
      // {
      //   dataField: "nama_customer",
      //   text: "Nama Customer",
      // },
      // {
      //   dataField: "no_hp",
      //   text: "No Hp",
      // },
      // {
      //   dataField: "alamat_customer",
      //   text: "Alamat Customer",
      // },
      {
        dataField: "berat_emas",
        text: "Berat",
        headerClasses: "text-right",
        formatter: (data) => {
          return <div className="text-right"> {data.toFixed(3)}</div>;
        },
        footer: (columnData) =>
        <div className="text-right"> {
          columnData.reduce((acc, item) => acc + item, 0).toFixed(3)} </div>
      },
      {
        dataField: "jumlah_hutang",
        text: "Jumlah Hutang",
        headerClasses: "text-right",
        formatter: (data) => {
          return <div className="text-right"> {parseInt(data).toLocaleString("kr-KO")}</div>;
        },
        footer: (columnData) =>
        <div className="text-right"> {parseInt(
          columnData.reduce((acc, item) => acc + item, 0)
        ).toLocaleString("kr-KO")} </div>
      },
      {
        dataField: "lama_pinjam",
        text: "Lama Pinjam",
        headerClasses: "text-right",
        formatter: (data) => {
          return <div className="text-right"> {data}</div>;
        },
       footer : ""
      },
      // {
      //   dataField: "total_hutang",
      //   text: "Total Hutang",
      //   headerClasses: "text-right",
      //   formatter: (data) => {
      //     return <div className="text-right"> {parseInt(data).toLocaleString("kr-KO")}</div>;
      //   },
      //   footer: (columnData) =>
      //   <div className="text-right"> {parseInt(
      //     columnData.reduce((acc, item) => acc + item, 0)
      //   ).toLocaleString("kr-KO")} </div>
      // },
    ]
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_hp"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Hutang Kosong"
              expandRow={expandRow}
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanHutangExel data={this.props.LaporanHutang} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormHutang = reduxForm({
  form: "HeadFormHutang",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadFormHutang);
// export default connect()(HeadFormHutang);
export default connect((state) => {
  return {
      initialValues: {
        tgl_awal: getTglSystem(),
        tgl_akhir: getTglSystem(),
      },
  };
})(HeadFormHutang);
