import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanHutangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {

    let total_berat_emas = 0;
    let total_berat_bruto = 0;
    let total_jumlah_hutang = 0;

    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        total_berat_emas = total_berat_emas + item.berat_emas;
        total_berat_bruto = total_berat_bruto + item.berat_bruto;
        total_jumlah_hutang = total_jumlah_hutang + item.jumlah_hutang;
      });
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Transaksi Hutang"
          sheet="Laporan Transaksi Hutang"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>

          <tr>
                <th colSpan="12"> Laporan Transaksi Hutang  </th>
          </tr>
          <tr>
                <th colSpan="12"> Periode  </th>
          </tr>
          <tr>
                <th colSpan="12"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal + 's/d' + JSON.parse(localStoragedecryp('tgl_laporan')).tgl_akhir} </th>
          </tr>
          <tr>
              <th colSpan="12"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="12"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
          <tr>
              <th colSpan="12"></th>
            </tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000" }}>TANGGAL HUTANG</th>
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>TANGGAL LUNAS</th> */}
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>TANGGAL JTH TEMPO</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE SALES</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NAMA CUSTOMER</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NO HP</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>ALAMAT CUSTOMER</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>BERAT EMAS</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>BERAT+PLASTIK</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>JUMLAH HUTANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>LAMA PINJAM</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>BUNGA</th>
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>TOTAL HUTANG</th> */}
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>TOTAL BAYAR</th> */}
            </tr>
        </thead>
        <tbody>
            
          {this.props.data.map((item, index) => (
              <>
              <tr>
                <th colSpan="12" style={{textAlign : "left"}}>No Faktur : {item._id.no_faktur_hutang} </th>
              </tr>
              
                {item.detail.map((row, index) => (
                    <>
                        <tr>
                            <td> {row.tgl_hutang}</td>
                            {/* <td> {row.tgl_lunas}</td> */}
                            <td> {row.tgl_tempo}</td>
                            <td> {row.kode_sales}</td>
                            <td> {row.nama_customer}</td>
                            <td> {row.no_hp}</td>
                            <td> {row.alamat_customer}</td>
                            <td style={{textAlign : "right" }}>&nbsp;{formatGram(row.berat_emas,3)}</td>
                            <td style={{textAlign : "right" }}>&nbsp;{formatGram(row.berat_bruto,3)}</td>
                            <td style={{textAlign : "right" }}> &nbsp;{row.jumlah_hutang.toLocaleString("kr-KO")}</td>
                            <td style={{textAlign : "right" }}> {row.lama_pinjam}</td>
                            <td style={{textAlign : "right" }}> {row.bunga}</td>
                            {/* <td style={{textAlign : "right" }}> &nbsp;{row.total_hutang.toLocaleString("kr-KO")}</td> */}
                            {/* <td style={{textAlign : "right" }}> &nbsp;{row.total_bayar.toLocaleString("kr-KO")}</td> */}
                        </tr>
                    </>
                ))}
                {/* <tr>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000" }} colSpan="6"> Subtotal :</th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{formatGram(item.detail.map(list => list.berat_emas).reduce((a,b) => a+b,0),3)} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{formatGram(item.detail.map(list => list.berat_bruto).reduce((a,b) => a+b,0),3)} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> &nbsp;{item.detail.map(list => list.jumlah_hutang).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> {item.detail.map(list => list.lama_pinjam).reduce((a,b) => a+b,0)} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> {item.detail.map(list => list.bunga).reduce((a,b) => a+b,0)} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> &nbsp;{item.detail.map(list => list.total_hutang).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </th>
                    <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> &nbsp;{item.detail.map(list => list.total_bayar).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </th>
                </tr> */}
            </>
          ))}
          </tbody>
          <tfoot>
          <tr>
          <th style={{ backgroundColor: "#E8E5E5", color: "#000" }} colSpan="6"> Grand Total :</th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>&nbsp;{ formatGram(total_berat_emas,3)} </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{formatGram(total_berat_bruto,3)}</th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{total_jumlah_hutang.toLocaleString("kr-KO")}</th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }} colSpan="2"></th>
          </tr>
          </tfoot>

        </table>
      </>
    );
  }
}
export default LaporanHutangExel;
