import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  formatGram,
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanHutangPdf = (data = "") => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_daftar = 0;
  let total_semua = 0;

  // let total_berat = 0;
  // let total_berat_bruto = 0;
  // let total_jumlah_hutang = 0;



  let grand_total_berat = 0;
  let grand_total_berat_bruto = 0;
  let grand_total_jumlah_hutang = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN TRANSAKSI HUTANG", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN TRANSAKSI HUTANG",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  tableColumn = [
    [
      {
        content: `TANGGAL HUTANG`,
      },
      // {
      //   content: `TANGGAL LUNAS`,
      // },
      {
        content: `TANGGAL JTH TEMPO`,
      },
      {
        content: `KODE SALES`,
      },
      {
        content: `NAMA CUSTOMER`,
      },
      {
        content: `NO HP`,
      },
      {
        content: `ALAMAT CUSTOMER`,
      },
      {
        content: `BERAT EMAS`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT+PLASTIK`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `JUMLAH HUTANG`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `LAMA PINJAM`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BUNGA`,
        styles: {
          halign: "right",
        },
      },
      // {
      //   content: `TOTAL HUTANG`,
      //   styles: {
      //     halign: "right",
      //   },
      // },
      // {
      //   content: `TOTAL BAYAR`,
      //   styles: {
      //     halign: "right",
      //   },
      // },
    ],
  ];
  data.forEach((item, index) => {
    // console.log(item._id.pembayaran[0].jenis);
    let head = [
      {
        content: `NO FAKTUR : ${item._id.no_faktur_hutang}`,
        colSpan: 13,
      },
    ];

    tableRows.push(head);
    let total_berat = 0;
    let total_berat_bruto = 0;
    let total_jumlah_hutang = 0;
  
    item.detail.forEach((item, index) => {
      let rows = [
        item.tgl_hutang,
        // item.tgl_lunas,
        item.tgl_tempo,
        item.kode_sales,
        item.nama_customer,
        item.no_hp,
        item.alamat_customer,
        {
          content: formatGram(item.berat_emas,3),
           styles: {
            halign: "right",
          },
        },
        {
          content:    formatGram(item.berat_bruto,3),
           styles: {
            halign: "right",
          },
        },
        {
          content:   parseInt(item.jumlah_hutang).toLocaleString("kr-KO"),
           styles: {
            halign: "right",
          },
        },
        {
          content:   item.lama_pinjam,
           styles: {
            halign: "right",
          },
        },
        {
          content:  item.bunga + "%",
           styles: {
            halign: "right",
          },
        },
        // {
        //   content:  parseInt(item.total_hutang).toLocaleString("kr-KO"),
        //    styles: {
        //     halign: "right",
        //   },
        // },
        // {
        //   content: parseInt(item.total_bayar).toLocaleString("kr-KO"),
        //    styles: {
        //     halign: "right",
        //   },
        // },
      ];
      total_berat = total_berat + item.berat_emas;
      total_berat_bruto = total_berat_bruto + item.berat_bruto;
      total_jumlah_hutang = total_jumlah_hutang + item.jumlah_hutang;

      grand_total_berat = grand_total_berat + item.berat_emas;
      grand_total_berat_bruto = grand_total_berat_bruto + item.berat_bruto;
      grand_total_jumlah_hutang =
        grand_total_jumlah_hutang + item.jumlah_hutang;

      tableRows.push(rows);
    });

    total_daftar = total_daftar + item.detail.length;
    total_semua = total_semua + item.detail.length;

    // let total = [
    //   {
    //     content: `Subtotal :`,
    //     colSpan: 6,
    //      styles: {
    //        fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${formatGram(total_berat,3)}`,
    //     styles: {
    //        fillColor: "#E8E5E5",
    //       textColor: "#000",
    //       halign: "right",

    //     },
    //   },
    //   {
    //     content: `${formatGram(total_berat_bruto,3)}`,
    //     styles: {
    //        fillColor: "#E8E5E5",
    //       textColor: "#000",
    //       halign: "right",

    //     },
    //   },
    //   {
    //     content: `${total_jumlah_hutang.toLocaleString("kr-KO")}`,
    //     styles: {
    //        fillColor: "#E8E5E5",
    //       textColor: "#000",
    //       halign: "right",

    //     },
    //   },
    //   {
    //     content: ``,
    //     colSpan: "3",
    //     styles: {
    //        fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    // ];
    // tableRows.push(total);
  });

  let grand_total = [
    {
      content: `Grand Total : `,
      colSpan: 6,
      styles: {
         fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${grand_total_berat.toFixed(3)}`,
      styles: {
         fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",

      },
    },
    {
      content: `${grand_total_berat_bruto.toFixed(3)}`,
      styles: {
         fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },
   
    {
      content: ``,
      colSpan: "3",
      styles: {
         fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(grand_total);
  
  let printed = [
    {
      colSpan: 3,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
       fillColor: "#E8E5E5",
      textColor: "#000",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];

  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(
  //   `User                          : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  // doc.text(`Total Daftar              : ${total_semua}`, 14, finalY + 22);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  doc.save(`LAPORAN TRANSAKSI HUTANG.pdf`);
  // var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  // var x = window.open();
  // x.document.open();
  // x.document.write(
  //   "<html><head><title>LAPORAN TRANSAKSI HUTANG</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
  //     string +
  //     "></embed></body></html>"
  // );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanHutangPdf;
